'use client';

import { useState } from 'react';
import { Device, MaintenanceRecord } from '@/types';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import {
  CheckCircle,
  Wrench,
  Calendar,
  FileText
} from 'lucide-react';

interface MaintenanceTrackerProps {
  device: Device;
  onMaintenanceCompleted: (deviceId: string, record: Omit<MaintenanceRecord, 'id'>) => void;
}

export function MaintenanceTracker({ device, onMaintenanceCompleted }: MaintenanceTrackerProps) {
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [technician, setTechnician] = useState(device.assignedContacts[0]?.name || '');
  const [cost, setCost] = useState('');
  const [notes, setNotes] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async () => {
    setIsSubmitting(true);

    const record: Omit<MaintenanceRecord, 'id'> = {
      deviceId: device.id,
      vehicleId: device.vehicleId,
      date: new Date(),
      type: 'preventive',
      description: `Maintenance completed for ${device.name}`,
      technician,
      cost: cost ? parseFloat(cost) : undefined,
      notes: notes || undefined,
      nextDueDate: new Date(Date.now() + device.maintenanceIntervalDays * 24 * 60 * 60 * 1000)
    };

    onMaintenanceCompleted(device.id, record);
    setIsDialogOpen(false);
    setIsSubmitting(false);
    setCost('');
    setNotes('');
  };

  const isOverdue = device.nextMaintenanceDate < new Date();
  const daysUntilDue = Math.ceil((device.nextMaintenanceDate.getTime() - Date.now()) / (1000 * 60 * 60 * 24));

  return (
    <div className="space-y-4">
      {/* Status and Action */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          {device.status === 'completed' ? (
            <CheckCircle className="h-5 w-5 text-green-600" />
          ) : isOverdue ? (
            <Wrench className="h-5 w-5 text-red-600" />
          ) : (
            <Calendar className="h-5 w-5 text-orange-600" />
          )}
          <span className="font-medium">
            {device.status === 'completed' ? 'Up to date' :
             isOverdue ? `Overdue by ${Math.abs(daysUntilDue)} days` :
             `Due in ${daysUntilDue} days`}
          </span>
        </div>

        {device.status !== 'completed' && (
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger>
              <Button size="sm" className="bg-green-600 hover:bg-green-700">
                <CheckCircle className="h-4 w-4 mr-2" />
                Mark Completed
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogHeader>
                <DialogTitle>Complete Maintenance</DialogTitle>
                <DialogDescription>
                  Record maintenance completion for {device.name}
                </DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="technician" className="text-right text-sm font-medium">
                    Technician
                  </label>
                  <input
                    id="technician"
                    value={technician}
                    onChange={(e) => setTechnician(e.target.value)}
                    className="col-span-3 flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                    placeholder="Enter technician name"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="cost" className="text-right text-sm font-medium">
                    Cost (QAR)
                  </label>
                  <input
                    id="cost"
                    type="number"
                    value={cost}
                    onChange={(e) => setCost(e.target.value)}
                    className="col-span-3 flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                    placeholder="Optional"
                  />
                </div>
                <div className="grid grid-cols-4 items-start gap-4">
                  <label htmlFor="notes" className="text-right text-sm font-medium pt-2">
                    Notes
                  </label>
                  <Textarea
                    id="notes"
                    value={notes}
                    onChange={(e) => setNotes(e.target.value)}
                    className="col-span-3"
                    placeholder="Maintenance notes..."
                    rows={3}
                  />
                </div>
              </div>
              <DialogFooter>
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setIsDialogOpen(false)}
                  disabled={isSubmitting}
                >
                  Cancel
                </Button>
                <Button
                  type="button"
                  onClick={handleSubmit}
                  disabled={isSubmitting || !technician.trim()}
                  className="bg-green-600 hover:bg-green-700"
                >
                  {isSubmitting ? 'Recording...' : 'Complete Maintenance'}
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        )}
      </div>

      {/* Maintenance History */}
      <div className="space-y-2">
        <h4 className="text-sm font-medium text-gray-700 flex items-center gap-2">
          <FileText className="h-4 w-4" />
          Maintenance History
        </h4>
        <div className="text-sm text-gray-600">
          {device.lastMaintenanceDate ? (
            <div className="flex items-center gap-2">
              <Calendar className="h-3 w-3" />
              <span>Last maintenance: {device.lastMaintenanceDate.toLocaleDateString()}</span>
            </div>
          ) : (
            <span className="text-gray-500 italic">No maintenance history available</span>
          )}
        </div>
      </div>
    </div>
  );
}
